<template>
  <div
    class="page-container report"
    v-if="currentMeasurement"
  >
    <div class="page-box">
      <div class="page-box-title report-title">
        <h3>Analyse, advies en akoestische producten</h3>
        <div class="flex-divider"></div>
        <div class="report-expand-switch field">
          <b-switch v-model="pageExpanded">Expert modus</b-switch>
        </div>
        <base-button
          type="secondary"
          @click="printAdvice"
        >
          <base-icon
            iconRef="printer"
            position="left"
          />Afdrukken
        </base-button>
      </div>
      <div class="report-content-wrapper">
        <div class="report-content is-two-thirds bg-primary-light condensed">Met Sonar, het door Rivasono ontwikkeld akoestisch calculatiesysteem, analyseren en berekenen wij snel en vakkundig hoe de huidige akoestiek is, de optimale akoestiek voor de betreffende ruimte en wat er nodig is om die akoestiek te realiseren. We hebben onze ervaring met inmiddels, meer dan 1250 uitgevoerde projecten erin verwerkt.</div>
        <div class="report-content report-disclaimer condensed">De calculaties zijn gebaseerd op akoestische modellen en schattingen voor algemene situaties. Eventuele metingen zijn ad-hoc
          uitgevoerd, zonder dat alle bouwkundige eigenschappen bekend zijn. De meetresultaten en calculaties zijn bedoeld ter indicatie; er kunnen geen rechten aan worden ontleend.</div>
      </div>
    </div>
    <div class="page-box">
      <div class="page-box-title report-title">
        <h3>Project</h3>
      </div>
      <div class="report-content-wrapper">
        <div class="report-content">
          <div class="value-row">
            <div class="value-group">
              <div class="label">Klant</div>
              <div class="value">{{currentClient.data().clientName}}</div>
            </div>
            <div class="value-group">
              <div class="label">Project</div>
              <div class="value">{{currentProject.data().projectName}}</div>
            </div>
            <div class="value-group">
              <div class="label">Ruimte</div>
              <div class="value">{{currentRoom.data().roomName}}, {{currentRoomArea | $twoDecimals | $dotToComma}} m<sup>2</sup></div>
            </div>
            <div class="value-group">
              <div class="label">Meting</div>
              <div class="value">{{currentMeasurement.data().type}}, {{currentMeasurementDatePretty}}, {{currentMeasurement.data().author}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="page-box">
      <div class="page-box-title report-title">
        <h3>Analyse</h3>
        <div class="flex-divider"></div>
        <base-button
          type="secondary"
          @click='openCorrectionPrompt'
        >
          <base-icon
            iconRef="options"
            position="left"
          />Correctie
        </base-button>
      </div>
      <div class="report-content-wrapper bg-primary-light">
        <div class="report-content is-two-thirds">
          <p>Analyse van de huidige akoestiek van de ruimte: <span class="value">{{currentRoom.data().roomName}}</span>, <br />op basis van de akoestische nulmeting, uitgevoerd op <span class="value">{{currentMeasurement.data().date.seconds | $prettifyDate}}</span> door <span class="value">{{currentMeasurement.data().author}}</span></p>
          <p>De galmtijd is gemiddeld <span
              class="value"
              :class="currentMeasurementQualityColor"
            >{{currentMeasurementWeightedAverage | $twoDecimals | $dotToComma}} s</span>.</p>
          <p>De huidige akoestische kwaliteit is daarmee <span
              class="value"
              :class="currentMeasurementQualityColor"
            >{{currentMeasurementQuality | $noDecimals}}%</span>.</p>
        </div>
        <div class="gauge-chart-box-wrapper">
          <div class="gauge-chart-box">
            <div class="gauge-label">
              <span>Kwaliteit huidige akoestiek:</span>
              <div class="gauge-value">
                <span :class="currentMeasurementQualityColor">{{currentMeasurementQuality | $noDecimals}}%</span>
              </div>
            </div>
            <div class="gauge">
              <highchart
                :options="currentMeasurementGaugeOptions"
                ref="currentMeasurementGauge"
              />
            </div>
          </div>
        </div>
      </div>
      <div
        class="report-expand-box"
        v-show="pageExpanded"
      >
        <div class="report-content-wrapper">
          <div class="report-content is-two-thirds">
            <p>De gemeten galmtijden bij 6 frequentiebanden, van 125 Hz t/m 4000 Hz:</p>
            <table class="report-table">
              <tr>
                <th>125 Hz</th>
                <th>250 Hz</th>
                <th>500 Hz</th>
                <th>1000 Hz</th>
                <th>2000 Hz</th>
                <th>4000 Hz</th>
              </tr>
              <tr>
                <td
                  v-for="value in currentMeasurementValues"
                  :key="value[0]"
                >{{value[1] | $twoDecimals | $dotToComma}}</td>
              </tr>
            </table>
          </div>
          <div class="report-content">
            <highchart
              :options="currentMeasurementChartOptions"
              ref="currentMeasurementChart"
            />
          </div>
        </div>
        <div class="report-content-wrapper condensed report-disclaimer">
          <div class="report-content">
            Om de akoestiek van de huidige, onbehandelde ruimte te bepalen, voeren wij een akoestische meting uit. Hierbij meten wij de galmtijden, volgens de RT60 norm, over 6
            frequentiegebieden. Hiermee kunnen we, naast de galmtijden ook het klankkarakter, de galmtijden bij lage, midden en hoge tonen, van de ruimte bepalen en meenemen in het advies.
          </div>
        </div>
      </div>
    </div>
    <div class="page-box">
      <div class="page-box-title report-title">
        <h3>Advies</h3>
      </div>
      <div class="report-content-wrapper bg-primary-light">
        <div class="report-content is-two-thirds">
          <p>Voor de ruimte <span class="value">{{currentRoom.data().roomName}}</span>, adviseren wij de volgende gemiddelde galmtijden:</p>
          <ul>
            <li><span class="value yellow">{{currentRoomTypeContextualAdviceValuesWeightedAverage70 | $twoDecimals | $dotToComma}} s</span> voor een hoorbare akoestische verbetering, met <span class="value yellow">70%</span> akoestische kwaliteit</li>
            <li><span class="value green">{{currentRoomTypeContextualAdviceValuesWeightedAverage80 | $twoDecimals | $dotToComma}} s</span> voor goede akoestiek, met <span class="value green">80%</span> akoestische kwaliteit</li>
            <li><span class="value green-dark">{{currentRoomTypeContextualAdviceValuesWeightedAverage | $twoDecimals | $dotToComma}} s</span> voor optimale akoestiek, met <span class="value green-dark">100%</span> akoestische kwaliteit</li>
          </ul>
        </div>
        <div class="report-content">
          <div class="score-table">
            <div class="score-table-row">
              <div class="score-table-value yellow">70%</div>
              <div class="score-table-label">Hoorbare verbetering</div>
              <div class="score-table-value yellow">{{currentRoomTypeContextualAdviceValuesWeightedAverage70 | $twoDecimals | $dotToComma}} s</div>
            </div>
            <div class="score-table-row">
              <div class="score-table-value green">80%</div>
              <div class="score-table-label">Goede akoestiek</div>
              <div class="score-table-value green">{{currentRoomTypeContextualAdviceValuesWeightedAverage80 | $twoDecimals | $dotToComma}} s</div>
            </div>
            <div class="score-table-row">
              <div class="score-table-value green-dark">100%</div>
              <div class="score-table-label">Optimale akoestiek</div>
              <div class="score-table-value green-dark">{{currentRoomTypeContextualAdviceValuesWeightedAverage | $twoDecimals | $dotToComma}} s</div>
            </div>
          </div>
        </div>
      </div>
      <div
        class="report-expand-box"
        v-show="pageExpanded"
      >
        <div class="report-content-wrapper">
          <div class="report-content is-two-thirds">
            <p>Geadviseerde galmtijden per frequentie:</p>
            <table class="report-table">
              <tr>
                <td></td>
                <th>125 Hz</th>
                <th>250 Hz</th>
                <th>500 Hz</th>
                <th>1000 Hz</th>
                <th>2000 Hz</th>
                <th>4000 Hz</th>
              </tr>
              <tr>
                <th class="align-right">Hoorbare verbetering (70%)</th>
                <td
                  class="yellow"
                  v-for="(value, index) in currentRoomTypeContextualAdviceValues70"
                  :key="index"
                >{{value | $twoDecimals | $dotToComma}}</td>
              </tr>
              <tr>
                <th class="align-right">Goede akoestiek (80%)</th>
                <td
                  class="green"
                  v-for="(value, index) in currentRoomTypeContextualAdviceValues80"
                  :key="index"
                >{{value | $twoDecimals | $dotToComma}}</td>
              </tr>
              <tr>
                <th class="align-right">Optimale akoestiek (100%)</th>
                <td
                  class="green-dark"
                  v-for="(value, index) in currentRoomTypeContextualAdviceValues"
                  :key="index"
                >{{value | $twoDecimals | $dotToComma}}</td>
              </tr>
            </table>
          </div>
          <div class="report-content">
            <highchart
              :options="analysisChartOptions"
              ref="analysisChart"
            />
          </div>
        </div>
        <div class="report-content-wrapper condensed report-disclaimer">
          <div class="report-content">
            Voor iedere ruimte is de optimale akoestiek te bepalen. Sonar berekent deze nauwkeurig, op basis van vele factoren. O.a. de afmetingen (inhoud) en het soort gebruik van de ruimte, de adviezen in het bouwbesluit, het gemiddeld aantal personen aanwezig en of deze stil of pratend zijn en bijzondere wensen, bijvoorbeeld een extra rustige omgeving, voor vertrouwelijke gesprekken. Tevens de optische indruk van de ruimte zoals plafondhoogte, afstand tot de wanden en de ambiance, bijv. modern, open, klassiek, besloten enz. Hiermee zorgen wij dat wat je straks hoort ook past bij wat je ziet.
          </div>
        </div>
      </div>
    </div>
    <div class="scenario-tabs page-break-before">
      <button
        class="button secondary tab"
        :class="{ active: scenario.active }"
        v-for="(scenario, scenarioIndex) in scenarios"
        :key="scenarioIndex"
        @click.self="setActiveScenario(scenarioIndex)"
      >
        <span
          class="scenario-tab-label"
          @click.self="setActiveScenario(scenarioIndex)"
        >{{scenarios[scenarioIndex].label}}</span>
        <div
          class="scenario-tab-buttons"
          v-show="scenario.active"
        >
          <b-tooltip
            label="Scenario label wijzigen"
            type="is-dark"
          >
            <base-button
              type="secondary"
              class="inline-button"
              :loading="savingAdvice"
              :disabled="savingAdvice"
              @click="editScenarioLabel(scenarioIndex)"
            >
              <base-icon iconRef="edit-2" />
            </base-button>
          </b-tooltip>
          <b-tooltip
            label="Scenario dupliceren"
            type="is-dark"
          >
            <base-button
              type="secondary"
              class="inline-button"
              :loading="savingAdvice"
              :disabled="savingAdvice"
              @click="duplicateScenario(scenarioIndex)"
            >
              <base-icon iconRef="copy" />
            </base-button>
          </b-tooltip>
          <b-tooltip
            label="Scenario verwijderen"
            type="is-dark"
          >
            <base-button
              type="secondary"
              class="inline-button"
              :loading="savingAdvice"
              :disabled="savingAdvice"
              @click="deleteScenario(scenarioIndex)"
            >
              <base-icon iconRef="trash" />
            </base-button>
          </b-tooltip>
        </div>
      </button>
      <base-button
        type="secondary"
        @click="addScenario"
      >
        <base-icon iconRef="plus" />
      </base-button>
    </div>
    <div class="page-box">
      <div class="page-box-title report-title has-sticky-child">
        <h3>Producten</h3>
      </div>
      <div class="report-content-wrapper bg-primary-light sticky">
        <div class="report-content is-two-thirds">
          <p>Geselecteerde producten:</p>
          <p><span class="value">{{currentScenarioProductsTotalArea | $twoDecimals | $dotToComma}} m<sup>2</sup></span>, dit levert een <span
              class="value"
              :class="currentMeasurementIncludingProductsQualityColor"
            >{{currentMeasurementIncludingProductsQualityVerdict}}</span> akoestiek op (<span
              class="value"
              :class="currentMeasurementIncludingProductsQualityColor"
            >{{currentMeasurementIncludingProductsQuality | $noDecimals}}%</span> akoestische kwaliteit),<br /> met een gemiddelde galmtijd van <span
              class="value"
              :class="currentMeasurementIncludingProductsQualityColor"
            >{{getWeightedAverage(rt60WithProducts) | $twoDecimals | $dotToComma}} s</span></p>
        </div>
        <div class="gauge-chart-box-wrapper">
          <div class="gauge-chart-box">
            <div class="gauge-label">
              <span>Kwaliteit akoestiek inclusief producten:</span>
              <div class="gauge-value">
                <span :class="currentMeasurementIncludingProductsQualityColor">{{currentMeasurementIncludingProductsQuality | $noDecimals}}%</span>
              </div>
            </div>
            <div class="gauge">
              <highchart
                :options="productsGaugeOptions"
                ref="productsGauge"
              />
            </div>
          </div>
        </div>
      </div>
      <div
        class="report-expand-box"
        v-show="pageExpanded"
      >
        <div class="report-content-wrapper">
          <div class="report-content is-two-thirds">
            <!--
            <p>Geadviseerde akoestische producten:</p>
            <ul>
              <li><span class="value yellow">12 m<sup>2</sup></span> voor een hoorbare akoestische verbetering, met <span class="value yellow">70%</span> akoestische kwaliteit</li>
              <li><span class="value green">19 m<sup>2</sup></span> voor goede akoestiek, met <span class="value green">80%</span> akoestische kwaliteit</li>
              <li><span class="value green-dark">26 m<sup>2</sup></span> voor optimale akoestiek, met <span class="value green-dark">100%</span> akoestische kwaliteit</li>
            </ul>
            <br />
            -->
            <p>Toegevoegde sabins per product per frequentie:</p>
            <table class="report-table">
              <tr>
                <td></td>
                <th>125 Hz</th>
                <th>250 Hz</th>
                <th>500 Hz</th>
                <th>1000 Hz</th>
                <th>2000 Hz</th>
                <th>4000 Hz</th>
              </tr>
              <tr
                v-for="(product, index) in currentScenario.addedProducts"
                :key="index"
              >
                <th class="align-right">{{product.productName}}</th>
                <td
                  v-for="(value, index) in product.ac"
                  :key="index"
                >{{value * product.totalArea / product.distance | $twoDecimals | $dotToComma}}</td>
              </tr>
            </table>
          </div>
          <div class="report-content">
            <highchart
              :options="productChartOptions"
              ref="productChart"
            />
          </div>
        </div>
      </div>
      <!-- Producten -->
      <div class="advice-product-cards-wrapper">
        <div
          class="advice-product-cards"
          :class="{active: scenario.active}"
          v-for="(scenario, scenarioIndex) in scenarios"
          :key="scenarioIndex"
          v-show="scenario.active"
        >
          <!-- Product card -->
          <h3 class="show-only-on-print page-break-before">{{scenario.label}}</h3>
          <div
            class="add-people-button-wrapper"
            v-show="scenario.hasPeople != true"
          >
            <base-button
              type="secondary"
              :loading="savingAdvice"
              :disabled="savingAdvice"
              @click="addPeople(scenarioIndex)"
            >
              <base-icon
                iconRef="plus"
                position="left"
              />Personen toevoegen
            </base-button>
          </div>
          <div
            class="advice-people-card"
            v-if="scenario.hasPeople == true"
          >
            <div class="value-row">
              <div class="value-group">
                <label
                  for=""
                  class="label"
                >Aantal personen gemiddeld aanwezig</label>
                <input
                  type="number"
                  name=""
                  id=""
                  class="input"
                  v-model="scenario.peopleAmount"
                  @keyup="saveAdvice()"
                >
                <div class="value show-only-on-print">{{scenario.peopleAmount}}</div>
              </div>
              <div class="value-group">
                <label
                  for=""
                  class="label"
                >Gedrag personen</label>
                <select
                  class="input"
                  v-bind:value="scenario.peopleType"
                  @change="scenario.peopleType = $event.target.value; saveAdvice()"
                >
                  <option
                    :value="peopleType.value"
                    v-for="peopleType in $peopleTypes"
                    :key="peopleType.value"
                  >{{peopleType.label}}</option>
                </select>
                <div class="value show-only-on-print">{{$peopleTypes.find(peopleType => peopleType.value == scenario.peopleType).label}}</div>
              </div>
            </div>
          </div>
          <div
            class="advice-product-card"
            v-for="(product, index) in scenarios[scenarioIndex].addedProducts"
            :key="index"
          >
            <div class="advice-product-card-half">
              <!-- Product Image -->
              <div class="advice-product-card-image">
                <img
                  :src="product.imgUrl"
                  alt="Rivasono designpaneel"
                >
              </div>
              <!-- Product Category -->
              <div class="advice-product-card-content">
                <div class="value-group product-category">
                  <div class="value">
                    {{product.category}}
                  </div>
                </div>
                <!-- Product Name -->
                <div class="value-group product-name">
                  <div class="value">
                    {{product.productName}}
                  </div>
                </div>
                <!-- Product Description -->
                <div class="value-row">
                  <div class="value-group product-description">
                    <div class="value">
                      {{product.description}}
                    </div>
                  </div>
                </div>
                <!-- Product Sizes, amount and note -->
                <div
                  class="value-row"
                  v-if="getWeightedAverage(product.ac) != 0"
                >
                  <div
                    class="value-group product-size"
                    v-if="!product.isRound"
                  >
                    <label class="label">Breedte, hoogte en opp.</label>
                    <div>
                      <span class="value">{{product.width | $dotToComma}}</span>
                      <span>m&nbsp;x&nbsp;</span>
                      <span class="value">{{product.height | $dotToComma}}</span>
                      <span>m&nbsp;=&nbsp;</span>
                      <span class="value">{{product.area | $dotToComma}}</span>
                      <span>m<sup>2</sup></span>
                    </div>
                  </div>
                  <div
                    class="value-group product-size"
                    v-if="product.isRound"
                  >
                    <label class="label">Diameter</label>
                    <div>
                      <span class="value">{{product.diameter | $dotToComma}} m</span>
                    </div>
                  </div>
                  <div class="value-group product-distance">
                    <label class="label">Afstand</label>
                    <div class="value">{{product.distanceLabel}}</div>
                  </div>
                </div>
                <div class="value-row">
                  <div class="value-group product-amount">
                    <label class="label">Aantal
                    </label>
                    <div class="flex-row">
                      <div>
                        <base-button
                          class="inline-button"
                          :loading="savingAdvice"
                          :disabled="savingAdvice"
                          @click="increaseProductAmount(product); calculateTotalArea(scenarioIndex, index); saveAdvice()"
                        >
                          <base-icon iconRef="plus" />
                        </base-button>
                        <span class="value">&nbsp;{{product.amount}}</span> stuks&nbsp;
                        <base-button
                          class="inline-button"
                          :loading="savingAdvice"
                          :disabled="savingAdvice"
                          @click="decreaseProductAmount(product); calculateTotalArea(scenarioIndex, index); saveAdvice()"
                        >
                          <base-icon iconRef="minus" />
                        </base-button>
                      </div>
                    </div>
                  </div>
                  <div
                    class="value-group product-totalarea"
                    v-if="getWeightedAverage(product.ac) != 0"
                  >
                    <label class="label">Opp. totaal</label>
                    <div class="value">{{product.totalArea | $dotToComma}} m<sup>2</sup></div>
                  </div>
                </div>
                <div
                  class="value-row"
                  v-if="product.note != ''"
                >
                  <div class="value-group product-note">
                    <label class="label">Opmerking</label>
                    <div class="value">{{product.note}}</div>
                  </div>
                </div>
              </div>
              <!-- Card Buttons -->
              <div class="product-card-buttons">
                <base-button
                  @click="editProduct(scenarioIndex, index)"
                  :loading="savingAdvice"
                  :disabled="savingAdvice"
                >
                  <base-icon iconRef="edit" />
                </base-button>
                <base-button
                  @click="duplicateProduct(scenarioIndex, index); saveAdvice()"
                  :loading="savingAdvice"
                  :disabled="savingAdvice"
                >
                  <base-icon iconRef="copy" />
                </base-button>
              </div>
            </div>
          </div>
          <div
            class="flex show-only-on-print"
            v-if="scenarios[scenarioIndex].addedProducts.length === 0"
          >
            <div class="flex-divider"></div>
            <p>Geen producten toegevoegd</p>
            <div class="flex-divider"></div>
          </div>
          <div class="add-product-button-wrapper">
            <base-button
              type="secondary"
              class="add-product-button"
              :loading="savingAdvice"
              :disabled="savingAdvice"
              @click="addProduct(scenarioIndex)"
            >
              <base-icon
                iconRef="plus"
                position="left"
              />Product toevoegen
            </base-button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal-container"
      v-if="productEditModalShown && productCurrentlyEditing != null"
    >
      <div class="modal-overlay">
        <div class="modal-box">
          <div class="modal-title">
            <h3>Bewerk Product</h3>
            <div class="flex-divider"></div>
            <base-button
              type="secondary"
              @click="cancelProductEditModal"
            >
              <base-icon iconRef="close" />
            </base-button>
          </div>
          <div class="modal-content">
            <div class="modal-content-section">
              <br />
              <div class="input-row">
                <b-field
                  label="Product"
                  class="products-autocomplete"
                >
                  <b-autocomplete
                    v-model="productCurrentlyEditing.productName"
                    placeholder="Kies een product..."
                    :open-on-focus="true"
                    :data="filterProductsListOn(productCurrentlyEditing.productName)"
                    field="productName"
                    class="product-autocomplete"
                    @input="changedProductCurrentlyEditing(); calculateProductCurrentlyEditingTotalArea();"
                  >
                  </b-autocomplete>
                </b-field>
              </div>
              <div class="input-row">
                <div
                  class="input-container"
                  v-show="!productCurrentlyEditing.isRound"
                >
                  <label class="label">Breedte</label>
                  <input
                    v-show="!productCurrentlyEditing.hasFixedSize"
                    type="number"
                    class="input value"
                    placeholder="0"
                    v-model="productCurrentlyEditing.width"
                    @keyup="calculateProductCurrentlyEditingArea(); calculateProductCurrentlyEditingTotalArea()"
                  >
                  <div
                    class="value"
                    v-show="productCurrentlyEditing.hasFixedSize"
                  >{{productCurrentlyEditing.width}} m</div>
                </div>
                <div
                  class="input-container"
                  v-show="!productCurrentlyEditing.isRound"
                >
                  <label class="label">Hoogte</label>
                  <input
                    v-show="!productCurrentlyEditing.hasFixedSize && !productCurrentlyEditing.isRound"
                    type="number"
                    class="input value"
                    placeholder="0"
                    v-model="productCurrentlyEditing.height"
                    @keyup="calculateProductCurrentlyEditingArea(); calculateProductCurrentlyEditingTotalArea()"
                  >
                  <div
                    class="value"
                    v-show="productCurrentlyEditing.hasFixedSize"
                  >{{productCurrentlyEditing.height}} m</div>
                </div>
                <div
                  class="input-container"
                  v-show="productCurrentlyEditing.isRound"
                >
                  <label class="label">Diameter</label>
                  <div class="value">{{productCurrentlyEditing.diameter}} m</div>
                </div>
                <div class="input-container">
                  <label class="label">Oppervlakte</label>
                  <div><span class="value">{{productCurrentlyEditing.area | $twoDecimals}}</span> m<sup>2</sup></div>
                </div>
              </div>
              <div class="input-row">
                <div class="input-container">
                  <label class="label">Aantal</label>
                  <input
                    type="number"
                    class="input value"
                    placeholder="0"
                    v-model="productCurrentlyEditing.amount"
                    @keyup="calculateProductCurrentlyEditingTotalArea()"
                  >
                </div>
                <div class="input-container">
                  <label class="label">Oppervlakte totaal</label>
                  <div><span class="value">{{productCurrentlyEditing.totalArea}}</span> m<sup>2</sup></div>
                </div>
              </div>
              <div class="input-row">
                <div class="input-container">
                  <label class="label">Afstand</label>
                  <select
                    class="input value"
                    v-bind:value="productCurrentlyEditing.distance"
                    @change="productCurrentlyEditing.distance = $event.target.value; productCurrentlyEditing.distanceLabel = $event.target.options[$event.target.selectedIndex].text"
                  >
                    <option value="1">Gemiddeld (2m tot 4m)</option>
                    <option value="0.9">Dichtbij (&lt; 2m)</option>
                    <option value="1.2">Op afstand (4m tot 8m)</option>
                    <option value="1.5">Op grote afstand (&gt; 8m)</option>
                  </select>
                </div>
              </div>
              <div class="input-row">
                <div class="input-container">
                  <label class="label">Opmerking</label>
                  <textarea
                    class="input value"
                    v-model="productCurrentlyEditing.note"
                  ></textarea>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <base-button
              @click="deleteProduct"
              type="danger"
            >Verwijderen
            </base-button>
            <div class="flex-divider"></div>
            <base-button
              @click="cancelProductEditModal"
              type="secondary"
            >Annuleren</base-button>
            <base-button @click="saveProduct">Opslaan</base-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Highcharts from "highcharts";
import { Chart } from "highcharts-vue";
import HighchartsMore from "highcharts/highcharts-more";

HighchartsMore(Highcharts);

Highcharts.setOptions({
  credits: {
    enabled: false,
    text: "Rivasono.nl",
    href: "https://rivasono.nl"
  },
  chart: {
    backgroundColor: "#ffffff00",
    style: {
      fontFamily: "'Roboto', sans-serif",
      width: "100%"
    },
    height: "100%"
  },
  data: {
    decimalPoint: ","
  },
  title: {
    style: { fontSize: "16px", fontFamily: "Roboto Condensed" }
  },
  tooltip: {
    enabled: false
  },
  legend: {
    enabled: false
  },
  pane: {
    startAngle: -150,
    endAngle: 150,
    background: [{ backgroundColor: "#FFFEFB" }]
  },
  yAxis: {
    title: {
      style: { display: "none" }
    },
    labels: {
      formatter() {
        const label = this.value;
        return label.toString().replace(/\./g, ",");
      }
    }
  },
  xAxis: {
    type: "category",
    categories: ["125 Hz", "250 Hz", "500 Hz", "1000 Hz", "2000 Hz", "4000 Hz"],
    title: {
      style: { display: "none" }
    }
  }
});

export default {
  name: "AnalysisAndAdvice",
  components: {
    highchart: Chart
  },
  data() {
    return {
      constant: 0.161,
      correction: 1,
      savingAdvice: false,
      scenarios: [
        {
          active: true,
          label: "Scenario 1",
          addedProducts: []
        }
      ],
      productCurrentlyEditingIndexes: null,
      productCurrentlyEditing: null,
      pageExpanded: false,
      dataIsSet: false,
      currentMeasurementGaugeOptions: {
        chart: {
          type: "gauge"
        },
        title: {
          style: {
            display: "none"
          },
          text: "Huidige akoestische score"
        },
        series: [
          {
            name: "Huidige akoestische score",
            data: [
              (Math.round(this.currentMeasurementQuality * 100) / 100).toFixed(
                2
              )
            ],
            dataLabels: {
              enabled: false
            },
            dial: {
              backgroundColor: "#0a475f",
              baseLength: "95%",
              radius: "95%",
              baseWidth: 4,
              topWidth: 2
            },
            pivot: {
              backgroundColor: "#0a475f"
            }
          }
        ],
        yAxis: {
          min: 0,
          max: 100,
          plotBands: [
            {
              from: 0,
              to: 70,
              color: "#e53935"
            },
            {
              from: 70,
              to: 80,
              color: "#ffab00"
            },
            {
              from: 80,
              to: 95,
              color: "#00c853"
            },
            {
              from: 95,
              to: 100,
              color: "#009940"
            }
          ]
        }
      },
      productsGaugeOptions: {
        chart: {
          type: "gauge"
        },
        title: {
          style: {
            display: "none"
          },
          text: "Huidige akoestische score"
        },
        series: [
          {
            name: "Huidige akoestische score",
            data: [
              (
                Math.round(
                  this.currentMeasurementIncludingProductsQuality * 100
                ) / 100
              ).toFixed(2)
            ],
            dataLabels: {
              enabled: false
            },
            dial: {
              backgroundColor: "#0a475f",
              baseLength: "95%",
              radius: "95%",
              baseWidth: 4,
              topWidth: 2
            },
            pivot: {
              backgroundColor: "#0a475f"
            }
          }
        ],
        yAxis: {
          min: 0,
          max: 100,
          plotBands: [
            {
              from: 0,
              to: 70,
              color: "#e53935"
            },
            {
              from: 70,
              to: 80,
              color: "#ffab00"
            },
            {
              from: 80,
              to: 95,
              color: "#00c853"
            },
            {
              from: 95,
              to: 100,
              color: "#009940"
            }
          ]
        }
      },
      currentMeasurementChartOptions: {
        title: {
          text: "Huidige galmtijden"
        },
        chart: {
          height: "56%",
          backgroundColor: "#fff"
        },
        series: [
          {
            type: "spline",
            name: "Huidige galmtijden",
            color: "#e53935",
            dataLabels: {
              enabled: true,
              formatter() {
                return (Math.round(this.y * 100) / 100)
                  .toFixed(2)
                  .replace(/\./g, ",");
              }
            },
            data: this.currentMeasurementValues
          }
        ]
      },
      analysisChartOptions: {
        title: {
          text:
            "Galmtijden voor optimaal, goed en hoorbaar akoestisch resultaat"
        },
        chart: {
          height: "56%",
          backgroundColor: "#fff"
        },
        series: [
          {
            type: "areasplinerange",
            name: "Optimaal",
            color: "#009940",
            fillOpacity: 0.5,
            lineWidth: 0,
            enableMouseTracking: false,
            marker: {
              enabled: false
            },
            dataLabels: {
              enabled: false
            },
            data: this.currentMeasurementAdviceAreas
          },
          {
            type: "areasplinerange",
            name: "Goed",
            color: "#00c853",
            fillOpacity: 0.5,
            lineWidth: 0,
            enableMouseTracking: false,
            marker: {
              enabled: false
            },
            dataLabels: {
              enabled: false
            },
            data: this.currentMeasurementAdviceAreas80
          },
          {
            type: "areasplinerange",
            name: "Hoorbaar",
            color: "#ffab00",
            fillOpacity: 0.5,
            lineWidth: 0,
            enableMouseTracking: false,
            marker: {
              enabled: false
            },
            dataLabels: {
              enabled: false
            },
            data: this.currentMeasurementAdviceAreas70
          },
          {
            type: "spline",
            name: "Huidige galmtijden",
            color: "#e53935",
            enableMouseTracking: false,
            dataLabels: {
              enabled: true,
              formatter() {
                return (Math.round(this.y * 100) / 100)
                  .toFixed(2)
                  .replace(/\./g, ",");
              }
            },
            data: this.currentMeasurementValues
          }
        ]
      },
      productChartOptions: {
        title: {
          text: "Galmtijden na het toepassen van de geselecteerde producten",
          style: {
            color: "#0080b3"
          }
        },
        chart: {
          height: "56%",
          backgroundColor: "#fff"
        },
        series: [
          {
            type: "areasplinerange",
            name: "Optimaal",
            color: "#009940",
            fillOpacity: 0.5,
            lineWidth: 0,
            enableMouseTracking: false,
            marker: {
              enabled: false
            },
            dataLabels: {
              enabled: false
            },
            data: this.currentMeasurementAdviceAreas
          },
          {
            type: "areasplinerange",
            name: "Goed",
            color: "#00c853",
            fillOpacity: 0.5,
            lineWidth: 0,
            enableMouseTracking: false,
            marker: {
              enabled: false
            },
            dataLabels: {
              enabled: false
            },
            data: this.currentMeasurementAdviceAreas80
          },
          {
            type: "areasplinerange",
            name: "Hoorbaar",
            color: "#ffab00",
            fillOpacity: 0.5,
            lineWidth: 0,
            enableMouseTracking: false,
            marker: {
              enabled: false
            },
            dataLabels: {
              enabled: false
            },
            data: this.currentMeasurementAdviceAreas70
          },
          {
            type: "spline",
            name: "Galmtijden incl. producten",
            color: "#009ad9",
            enableMouseTracking: false,
            dataLabels: {
              enabled: true,
              formatter() {
                return (Math.round(this.y * 100) / 100)
                  .toFixed(2)
                  .replace(/\./g, ",");
              }
            },
            data: this.rt60WithProductsValues
          },
          {
            type: "spline",
            name: "Huidige galmtijden",
            color: "#e53935",
            enableMouseTracking: false,
            dataLabels: {
              enabled: true,
              formatter() {
                return (Math.round(this.y * 100) / 100)
                  .toFixed(2)
                  .replace(/\./g, ",");
              }
            },
            data: this.currentMeasurementValues
          }
        ]
      }
    };
  },
  watch: {
    currentRoom() {
      if (this.currentRoom) this.loadAdviceIfRoomHasOne();
    },
    rt60WithProductsValues() {
      this.$refs.productChart.chart.series[3].update({
        data: this.rt60WithProductsValues
      });
      this.$refs.productsGauge.chart.series[0].update({
        data: [this.currentMeasurementIncludingProductsQuality]
      });
    }
  },
  computed: {
    currentMeasurement() {
      return this.$store.getters.getMeasurementsList.find(measurement => {
        return measurement.id == this.$route.params.measurementId;
      });
    },
    currentMeasurementDisplayName() {
      let displayName = this.currentMeasurement.data().type;
      if (this.currentMeasurement.data().label.length > 0)
        displayName += `, ${this.currentMeasurement.data().label}`;
      return displayName;
    },
    currentRoom() {
      return this.$store.getters.getRoomsList.find(room => {
        return room.id == this.currentMeasurement.data().belongsTo;
      });
    },
    currentProject() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.currentRoom.data().belongsTo;
      });
    },
    currentClient() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    },
    currentMeasurementDatePretty() {
      return this.$prettifyDate(this.currentMeasurement.data().date.seconds);
    },
    currentMeasurementRT60() {
      return [
        this.currentMeasurement.data().hz125,
        this.currentMeasurement.data().hz250,
        this.currentMeasurement.data().hz500,
        this.currentMeasurement.data().hz1000,
        this.currentMeasurement.data().hz2000,
        this.currentMeasurement.data().hz4000
      ];
    },
    currentMeasurementValues() {
      return [
        ["125 Hz", this.currentMeasurementRT60[0]],
        ["250 Hz", this.currentMeasurementRT60[1]],
        ["500 Hz", this.currentMeasurementRT60[2]],
        ["1000 Hz", this.currentMeasurementRT60[3]],
        ["2000 Hz", this.currentMeasurementRT60[4]],
        ["4000 Hz", this.currentMeasurementRT60[5]]
      ];
    },
    currentMeasurementWeightedAverage() {
      return this.getWeightedAverage(this.currentMeasurementRT60);
    },
    currentMeasurementQuality() {
      let value =
        (this.currentRoomTypeContextualAdviceValuesWeightedAverage /
          this.currentMeasurementWeightedAverage) *
        100;
      if (value < 100) {
        return value;
      } else {
        return 100;
      }
    },
    currentMeasurementQualityColor() {
      if (this.currentMeasurementQuality <= 70) {
        return "red";
      } else if (this.currentMeasurementQuality <= 80) {
        return "yellow";
      } else if (this.currentMeasurementQuality <= 95) {
        return "green";
      } else {
        return "green-dark";
      }
    },
    currentMeasurementIncludingProductsQuality() {
      let value =
        (this.currentRoomTypeContextualAdviceValuesWeightedAverage /
          this.getWeightedAverage(this.rt60WithProducts)) *
        100;
      if (value < 100) {
        return value;
      } else {
        return 100;
      }
    },
    currentMeasurementIncludingProductsQualityColor() {
      if (this.currentMeasurementIncludingProductsQuality <= 70) {
        return "red";
      } else if (this.currentMeasurementIncludingProductsQuality <= 80) {
        return "yellow";
      } else if (this.currentMeasurementIncludingProductsQuality <= 95) {
        return "green";
      } else {
        return "green-dark";
      }
    },
    currentMeasurementIncludingProductsQualityVerdict() {
      if (this.currentMeasurementIncludingProductsQuality <= 70) {
        return "slechte";
      } else if (this.currentMeasurementIncludingProductsQuality <= 80) {
        return "hoorbare verbetering van de";
      } else if (this.currentMeasurementIncludingProductsQuality <= 95) {
        return "goede";
      } else {
        return "optimale";
      }
    },
    currentMeasurementAdviceAreas() {
      const array = [];
      const labels = [
        "125 Hz",
        "250 Hz",
        "500 Hz",
        "1000 Hz",
        "2000 Hz",
        "4000 Hz"
      ];
      this.currentRoomTypeContextualAdviceValues.forEach((value, index) => {
        array.push([labels[index], value, value / 0.95]);
      });
      return array;
    },
    currentMeasurementAdviceAreas80() {
      const array = [];
      const labels = [
        "125 Hz",
        "250 Hz",
        "500 Hz",
        "1000 Hz",
        "2000 Hz",
        "4000 Hz"
      ];
      this.currentRoomTypeContextualAdviceValues.forEach((value, index) => {
        array.push([labels[index], value / 0.95, value / 0.8]);
      });
      return array;
    },
    currentMeasurementAdviceAreas70() {
      const array = [];
      const labels = [
        "125 Hz",
        "250 Hz",
        "500 Hz",
        "1000 Hz",
        "2000 Hz",
        "4000 Hz"
      ];
      this.currentRoomTypeContextualAdviceValues.forEach((value, index) => {
        array.push([labels[index], value / 0.8, value / 0.7]);
      });
      return array;
    },
    allRoomTypeOptions() {
      return this.$store.getters.getAdvicevaluesList;
    },
    currentRoomType() {
      return this.allRoomTypeOptions.find(
        roomType => roomType.id == this.currentRoom.data().type
      );
    },
    currentRoom() {
      return this.$store.getters.getRoomsList.find(room => {
        return room.id == this.currentMeasurement.data().belongsTo;
      });
    },
    currentRoomArea() {
      return this.currentRoom.data().width * this.currentRoom.data().length;
    },
    currentRoomVolume() {
      return this.currentRoomArea * this.currentRoom.data().height;
    },
    currentRoomTypeAdviceValues() {
      if (this.currentRoomType.values[0].volume == 0) {
        return this.currentRoomType.values
          .slice()
          .reverse()
          .find(value => {
            return this.currentRoomVolume > value.volume;
          }).rt60;
      } else {
        return this.currentRoomType.values.find(value => {
          return this.currentRoomVolume > value.volume;
        }).rt60;
      }
    },
    currentRoomTypeContextualAdviceValues() {
      const array = [];
      this.currentRoomTypeAdviceValues.forEach(value => {
        array.push(
          value *
            this.currentRoom.data().interior *
            this.currentRoom.data().wishes *
            this.correction
        );
      });
      return array;
    },
    currentRoomTypeContextualAdviceValues80() {
      const array = [];
      this.currentRoomTypeContextualAdviceValues.forEach(value => {
        array.push(value / 0.8);
      });
      return array;
    },
    currentRoomTypeContextualAdviceValues70() {
      const array = [];
      this.currentRoomTypeContextualAdviceValues.forEach(value => {
        array.push(value / 0.7);
      });
      return array;
    },
    currentRoomTypeContextualAdviceValuesWeightedAverage() {
      return this.getWeightedAverage(
        this.currentRoomTypeContextualAdviceValues
      );
    },
    currentRoomTypeContextualAdviceValuesWeightedAverage80() {
      return this.currentRoomTypeContextualAdviceValuesWeightedAverage / 0.8;
    },
    currentRoomTypeContextualAdviceValuesWeightedAverage70() {
      return this.currentRoomTypeContextualAdviceValuesWeightedAverage / 0.7;
    },
    productsList() {
      return this.$store.getters.getProductsList;
    },
    filteredProductsList() {
      const filteredProductsList = [];
      this.productsList.forEach(product => {
        filteredProductsList.push({
          active: product.field_364,
          productName: product.field_125,
          brand: product.field_126,
          sku: product.field_127,
          category: product.field_365,
          width: product.field_374_raw,
          height: product.field_375_raw,
          diameter: product.field_376_raw,
          ac: [
            product.field_368_raw,
            product.field_369_raw,
            product.field_370_raw,
            product.field_371_raw,
            product.field_372_raw,
            product.field_373_raw
          ],
          description: product.field_366,
          priceEach: product.field_378_raw,
          pricePerM2: product.field_384_raw,
          imgUrl: product.field_385_raw.url
        });
      });
      return this.$sortProductsAlphabetically(
        filteredProductsList,
        "productName"
      );
    },
    uniqueProductCategories() {
      return [...new Set(this.filteredProductsList.map(item => item.category))];
    },
    filteredProductsSortedByCategory() {
      const filteredProductsSortedByCategory = [];
      this.uniqueProductCategories.forEach((category, categoryIndex) => {
        filteredProductsSortedByCategory.push(
          this.filteredProductsList.filter(product => {
            return (
              product.category == this.uniqueProductCategories[categoryIndex]
            );
          })
        );
      });
      return filteredProductsSortedByCategory;
    },
    zeroMeasurementAbsorbtion() {
      const array = [];
      this.currentMeasurementRT60.forEach(value => {
        array.push((this.constant * this.currentRoomVolume) / value);
      });
      return array;
    },
    productAbsorbtion() {
      const array = [0, 0, 0, 0, 0, 0];
      if (this.currentScenario.hasPeople) {
        const peopleAmount = this.currentScenario.peopleAmount;
        const peopleTypeSabins = this.$peopleTypes.find(
          peopleType => peopleType.value == this.currentScenario.peopleType
        ).sabins;
        for (let i = 0; i < 6; i++) {
          array[i] += peopleAmount * peopleTypeSabins[i];
        }
      }
      this.currentScenario.addedProducts.forEach(product => {
        for (let i = 0; i < 6; i++) {
          array[i] += (product.ac[i] * product.totalArea) / product.distance;
        }
      });
      return array;
    },
    rt60WithProducts() {
      const totalAbsorbtion = [];
      for (let i = 0; i < 6; i++) {
        totalAbsorbtion[i] =
          this.zeroMeasurementAbsorbtion[i] + this.productAbsorbtion[i];
      }
      const array = [];
      totalAbsorbtion.forEach(value => {
        array.push((this.currentRoomVolume * this.constant) / value);
      });
      return array;
    },
    rt60WithProductsValues() {
      return [
        ["125 Hz", this.rt60WithProducts[0]],
        ["250 Hz", this.rt60WithProducts[1]],
        ["500 Hz", this.rt60WithProducts[2]],
        ["1000 Hz", this.rt60WithProducts[3]],
        ["2000 Hz", this.rt60WithProducts[4]],
        ["4000 Hz", this.rt60WithProducts[5]]
      ];
    },
    currentScenario() {
      return this.scenarios.find(scenario => {
        return scenario.active;
      });
    },
    currentScenarioHasNoProducts() {
      return this.currentScenario.addedProducts.length == 0;
    },
    currentScenarioProductsTotalArea() {
      let value = 0;
      this.currentScenario.addedProducts.forEach(product => {
        value += product.totalArea;
      });
      return value;
    },
    graphMax() {
      let largestValue = 0;
      this.currentMeasurementValues.forEach(value => {
        if (value[1] > largestValue) {
          largestValue = value[1];
        }
      });
      this.currentRoomTypeContextualAdviceValues70.forEach(value => {
        if (value > largestValue) {
          largestValue = value;
        }
      });
      return Math.ceil(largestValue * 10) / 10;
    },
    graphMin() {
      let smallestValue = 9001;
      this.currentMeasurementValues.forEach(value => {
        if (value[1] < smallestValue) {
          smallestValue = value[1];
        }
      });
      this.currentRoomTypeContextualAdviceValues.forEach(value => {
        if (value < smallestValue) {
          smallestValue = value;
        }
      });
      return Math.floor(smallestValue * 10) / 10;
    },
    productEditModalShown() {
      return this.$store.getters.getModalStates.product.edit.shown;
    }
  },
  methods: {
    editProduct(scenarioIndex, productIndex) {
      this.productCurrentlyEditingIndexes = [scenarioIndex, productIndex];
      this.productCurrentlyEditing = Object.assign(
        {},
        this.scenarios[this.productCurrentlyEditingIndexes[0]].addedProducts[
          this.productCurrentlyEditingIndexes[1]
        ]
      );
      this.openProductEditModal();
    },
    cancelProductEditModal() {
      this.closeProductEditModal();
      if (
        this.scenarios[this.productCurrentlyEditingIndexes[0]].addedProducts[
          this.productCurrentlyEditingIndexes[1]
        ].sku == "0"
      ) {
        this.scenarios[
          this.productCurrentlyEditingIndexes[0]
        ].addedProducts.splice(this.productCurrentlyEditingIndexes[1], 1);
      }
      this.productCurrentlyEditingIndexes = null;
      this.productCurrentlyEditing = null;
    },
    saveProduct() {
      this.closeProductEditModal();
      this.scenarios[this.productCurrentlyEditingIndexes[0]].addedProducts[
        this.productCurrentlyEditingIndexes[1]
      ] = Object.assign({}, this.productCurrentlyEditing);
      this.productCurrentlyEditingIndexes = null;
      this.productCurrentlyEditing = null;
      this.saveAdvice();
    },
    openProductEditModal() {
      document.querySelector("html").classList.add("modal-is-open");
      this.$store.commit("setModalShown", {
        dataType: "product",
        modalType: "edit",
        shown: true
      });
    },
    closeProductEditModal() {
      document.querySelector("html").classList.remove("modal-is-open");
      this.$store.commit("setModalShown", {
        dataType: "product",
        modalType: "edit",
        shown: false
      });
    },
    setData() {
      this.currentMeasurementGaugeOptions.series[0].data = [
        this.currentMeasurementQuality
      ];

      this.currentMeasurementChartOptions.series[0].data = this.currentMeasurementValues;

      this.analysisChartOptions.series[0].data = this.currentMeasurementAdviceAreas;
      this.analysisChartOptions.series[1].data = this.currentMeasurementAdviceAreas80;
      this.analysisChartOptions.series[2].data = this.currentMeasurementAdviceAreas70;
      this.analysisChartOptions.series[3].data = this.currentMeasurementValues;

      this.productChartOptions.series[0].data = this.currentMeasurementAdviceAreas;
      this.productChartOptions.series[1].data = this.currentMeasurementAdviceAreas80;
      this.productChartOptions.series[2].data = this.currentMeasurementAdviceAreas70;
      this.productChartOptions.series[3].data = this.rt60WithProductsValues;
      this.productChartOptions.series[4].data = this.currentMeasurementValues;

      this.$refs.currentMeasurementChart.chart.yAxis[0].update({
        max: this.graphMax
      });
      this.$refs.analysisChart.chart.yAxis[0].update({
        max: this.graphMax
      });
      this.$refs.productChart.chart.yAxis[0].update({ max: this.graphMax });
      this.$refs.currentMeasurementChart.chart.yAxis[0].update({
        min: this.graphMin
      });
      this.$refs.analysisChart.chart.yAxis[0].update({
        min: this.graphMin
      });
      this.$refs.productChart.chart.yAxis[0].update({ min: this.graphMin });
    },
    getWeightedAverage(array) {
      let total = 0;
      for (let i = 0; i < 6; i++) {
        total += array[i];
      }
      for (let i = 1; i < 5; i++) {
        total += array[i];
      }
      return total / 10;
    },
    addPeople(scenarioIndex) {
      this.$set(this.scenarios[scenarioIndex], "hasPeople", true);
      this.$set(this.scenarios[scenarioIndex], "peopleAmount", 0);
      this.$set(this.scenarios[scenarioIndex], "peopleType", 0);
    },
    openCorrectionPrompt() {
      this.$dialog.prompt({
        message: "Correctiefactor:",
        inputAttrs: {
          type: "number",
          value: this.correction,
          min: 0,
          step: "0.01"
        },
        cancelText: "Annuleren",
        confirmText: "Opslaan",
        onConfirm: value => {
          this.correction = value;
          this.saveAdvice();
        }
      });
    },
    saveAdvice() {
      this.savingAdvice = true;
      console.log("Saving advice");
      this.$firestore
        .collection("rooms")
        .doc(this.currentRoom.id)
        .set(
          {
            scenarios: this.scenarios,
            hasAdvice: true,
            correction: this.correction
          },
          { merge: true }
        )
        .then(() => {
          console.log("Advice saved");
          this.$store.dispatch("fetchRooms").then(() => {
            this.savingAdvice = false;
          });
        })
        .catch(error => {
          this.savingAdvice = false;
          console.log("Error saving advice: ", error);
        });
    },
    printAdvice() {
      window.print();
    },
    toggleEdit(scenarioIndex, productIndex) {
      this.scenarios[scenarioIndex].addedProducts[productIndex].editing = !this
        .scenarios[scenarioIndex].addedProducts[productIndex].editing;
    },
    changedProductCurrentlyEditing() {
      const currentProduct = this.productCurrentlyEditing;
      const selectedProduct = this.filteredProductsList.find(product => {
        return product.productName == currentProduct.productName;
      });
      if (selectedProduct) {
        currentProduct.productName = selectedProduct.productName;
        currentProduct.description = selectedProduct.description;
        currentProduct.category = selectedProduct.category;
        currentProduct.sku = selectedProduct.sku;
        currentProduct.width = selectedProduct.width / 100;
        currentProduct.height = selectedProduct.height / 100;
        currentProduct.diameter = selectedProduct.diameter / 100;
        currentProduct.area = selectedProduct.area;
        currentProduct.ac = selectedProduct.ac;
        currentProduct.imgUrl = selectedProduct.imgUrl;
        if (selectedProduct.width != 0 && selectedProduct.height != 0) {
          currentProduct.hasFixedSize = true;
          currentProduct.isRound = false;
          currentProduct.area = currentProduct.width * currentProduct.height;
        } else if (selectedProduct.diameter != 0) {
          currentProduct.isRound = true;
          currentProduct.hasFixedSize = true;
          currentProduct.area = this.calculateCircleArea(
            currentProduct.diameter
          );
        } else {
          currentProduct.hasFixedSize = false;
          currentProduct.isRound = false;
          currentProduct.area = 0;
        }
        if (
          selectedProduct.ac.every(value => {
            return value == 0;
          })
        ) {
          currentProduct.nonAcoustic = true;
        } else {
          currentProduct.nonAcoustic = false;
        }
        if (selectedProduct.imgUrl == undefined) {
          currentProduct.imgUrl =
            "https://dummyimage.com/480x270/ffffff/009ad9.png&text=++Rivasono++";
        }
      }
    },
    changed(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      const selectedProduct = this.filteredProductsList.find(product => {
        return product.productName == currentProduct.productName;
      });
      if (selectedProduct) {
        currentProduct.productName = selectedProduct.productName;
        currentProduct.description = selectedProduct.description;
        currentProduct.category = selectedProduct.category;
        currentProduct.sku = selectedProduct.sku;
        currentProduct.width = selectedProduct.width / 100;
        currentProduct.height = selectedProduct.height / 100;
        currentProduct.diameter = selectedProduct.diameter / 100;
        currentProduct.area = selectedProduct.area;
        currentProduct.ac = selectedProduct.ac;
        currentProduct.imgUrl = selectedProduct.imgUrl;
        if (selectedProduct.width != 0 && selectedProduct.height != 0) {
          currentProduct.hasFixedSize = true;
          currentProduct.isRound = false;
          currentProduct.area = currentProduct.width * currentProduct.height;
        } else if (selectedProduct.diameter != 0) {
          currentProduct.isRound = true;
          currentProduct.hasFixedSize = true;
          currentProduct.area = this.calculateCircleArea(
            currentProduct.diameter
          );
        } else {
          currentProduct.hasFixedSize = false;
          currentProduct.isRound = false;
          currentProduct.area = 0;
        }
        if (
          selectedProduct.ac.every(value => {
            return value == 0;
          })
        ) {
          currentProduct.nonAcoustic = true;
        } else {
          currentProduct.nonAcoustic = false;
        }
        if (selectedProduct.imgUrl == undefined) {
          currentProduct.imgUrl =
            "https://dummyimage.com/480x270/ffffff/009ad9.png&text=++Rivasono++";
        }
      }
    },
    increaseProductAmount(product) {
      product.amount++;
    },
    decreaseProductAmount(product) {
      if (product.amount > 0) product.amount--;
    },
    calculateProductCurrentlyEditingArea() {
      const currentProduct = this.productCurrentlyEditing;
      if (currentProduct.isRound) {
        currentProduct.area = this.calculateCircleArea(currentProduct.diameter);
      } else {
        currentProduct.area = this.roundTo(
          Number(currentProduct.height) * Number(currentProduct.width),
          2
        );
      }
    },
    calculateArea(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      if (currentProduct.isRound) {
        currentProduct.area = this.calculateCircleArea(currentProduct.diameter);
      } else {
        currentProduct.area = this.roundTo(
          Number(currentProduct.height) * Number(currentProduct.width),
          2
        );
      }
    },
    calculateProductCurrentlyEditingTotalArea() {
      const currentProduct = this.productCurrentlyEditing;
      currentProduct.totalArea = this.roundTo(
        Number(currentProduct.area) * Number(currentProduct.amount),
        2
      );
    },
    calculateTotalArea(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      currentProduct.totalArea = this.roundTo(
        Number(currentProduct.area) * Number(currentProduct.amount),
        2
      );
    },
    calculateCircleArea(diameter) {
      const radius = Number(diameter) / 2;
      return this.roundTo(Math.PI * radius * radius, 2);
    },
    addScenario() {
      this.scenarios.push({
        active: false,
        label: `Scenario ${this.scenarios.length + 1}`,
        editingLabel: false,
        addedProducts: []
      });
      this.setActiveScenario(this.scenarios.length - 1);
    },
    editScenarioLabel(scenarioIndex) {
      this.$dialog.prompt({
        message: "Verander scenario label",
        inputAttrs: {
          type: "text",
          value: this.scenarios[scenarioIndex].label
        },
        cancelText: "Annuleren",
        confirmText: "Opslaan",
        onConfirm: value => {
          this.scenarios[scenarioIndex].label = value;
          this.saveAdvice();
        }
      });
    },
    deleteScenario(scenarioIndex) {
      if (this.scenarios.length == 1) return;
      if (scenarioIndex == 0) {
        this.scenarios[1].active = true;
      } else if (scenarioIndex >= 1) {
        this.scenarios[scenarioIndex - 1].active = true;
      }
      this.scenarios.splice(scenarioIndex, 1);
      this.saveAdvice();
    },
    setActiveScenario(scenarioIndex) {
      this.scenarios.forEach(scenario => {
        scenario.active = false;
      });
      this.scenarios[scenarioIndex].active = true;
    },
    addProduct(scenarioIndex) {
      this.scenarios[scenarioIndex].addedProducts.push({
        category: "",
        productName: "",
        sku: "0",
        ac: [0, 0, 0, 0, 0, 0],
        height: "",
        width: "",
        area: "",
        diameter: "",
        amount: "0",
        distance: 1,
        distanceLabel: "Gemiddeld (2m tot 4m)",
        totalArea: 0,
        note: "",
        hasFixedSize: false,
        isRound: false,
        imgUrl:
          "https://dummyimage.com/480x270/ffffff/333333.png&text=++afbeelding++",
        editing: true
      });
      const productIndex =
        this.scenarios[scenarioIndex].addedProducts.length - 1;
      this.productCurrentlyEditingIndexes = [scenarioIndex, productIndex];
      this.productCurrentlyEditing = Object.assign(
        {},
        this.scenarios[scenarioIndex].addedProducts[productIndex]
      );
      this.openProductEditModal();
    },
    deleteProduct() {
      this.closeProductEditModal();
      this.scenarios[
        this.productCurrentlyEditingIndexes[0]
      ].addedProducts.splice(this.productCurrentlyEditingIndexes[1], 1);
      this.productCurrentlyEditingIndexes = null;
      this.productCurrentlyEditing = null;
      this.saveAdvice();
    },
    duplicateProduct(scenarioIndex, productIndex) {
      const productToDuplicate = Object.assign(
        {},
        this.scenarios[scenarioIndex].addedProducts[productIndex]
      );
      this.scenarios[scenarioIndex].addedProducts.splice(
        productIndex + 1,
        0,
        productToDuplicate
      );
    },
    duplicateScenario(scenarioIndex) {
      const scenarioToDuplicate = Object.assign(
        {},
        this.scenarios[scenarioIndex]
      );
      scenarioToDuplicate.label += " - kopie";
      scenarioToDuplicate.active = false;
      this.scenarios.splice(scenarioIndex + 1, 0, scenarioToDuplicate);
      this.saveAdvice();
    },
    loadAdviceIfRoomHasOne() {
      if (this.currentRoom.data().hasAdvice === true) {
        this.scenarios = this.currentRoom.data().scenarios;
        this.correction = this.currentRoom.data().correction;
      }
    },
    roundTo(value, sig) {
      return Number(value.toFixed(sig));
    },
    filterProductsListOn(value) {
      return this.filteredProductsList.filter(product => {
        return product.productName.toLowerCase().includes(value.toLowerCase());
      });
    }
  },
  mounted() {
    this.$setPageTitle(`Advies - ${this.currentMeasurementDisplayName}`);
    if (this.currentRoom) this.loadAdviceIfRoomHasOne();
    this.setData();
  }
};
</script>


